// reset everything
* {
    margin:0;
    padding: 0;
    border:0;
    box-sizing: border-box;
}
html {
    // reset the font zice to 10px;
    font-size: 62.5%;
}

html,
body{
    font-fammily: "Helvetica Neue", Helvetica,Arial,sans-serif;
}

.content {
    margin-top:5.2rem;
}

.header__wrapper {
    background-color: $color-pizazz;

    h1 {
        color: $color-white;
        font-size: 3.6rem;
        font-weight: normal;
        padding: 1rem 1.5rem;
        text-align: center;
    }
}

.header__body {
    background-image: url('asset/image/robertoblake2016header.jpg');
    height: 61.1rem;
    background-position: center center;
    background-size: cover;
}

.header__footer {
    text-align: center;
    padding: 1.5rem;
}

.header__download {
    border:0.1rem solid $color-fuel-yellow;
    background-color: $color-sandy-brown;
    text-decoration: none;
    font-size: 2.5rem;
    color: $color-white;
    //margin: 0.5rem;
    padding: 0.5 1rem;
}